import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";



@Injectable({
  providedIn: "root",
})
export class SuppliersService {

  constructor(private http: HttpClient) { }

  getSuppliers(): Observable<any> {
    const SupplierssUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/all`;
    return this.http.get<any[]>(SupplierssUrl);
  }
  getSuppliersByStatus(params: any): Observable<any> {
    const SupplierssUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/all/by/status`;
    return this.http.get<any>(SupplierssUrl, {params});
  }
  
  getSuppliersByCategory(params: any): Observable<any> {
    const SupplierssUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/category`;
    return this.http.get<any>(SupplierssUrl, {params});
  }
  

  getSuppliersById(params: any): Observable<any> {
    const SupplierssUrl= `${environment.baseUrlProcurement}/api/v1/suppliers/find/by/id`;

    return this.http.get<any>(SupplierssUrl, {params});
  }

  getSuppliersByCode(params: any): Observable<any> {
    const SupplierssUrl= `${environment.baseUrlProcurement}/api/v1/suppliers/find/by/supplierCode`;

    return this.http.get<any>(SupplierssUrl, {params});
  }

  addSuppliers(data: any): Observable<any> {
    const SupplierssUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/create`;
    return this.http.post<any>(SupplierssUrl, data);
  }

  updateSuppliers(SuppliersDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/update`;
    return this.http.put<any>(updateBillUrl, SuppliersDetails)
  }

  deleteSuppliersTemporarily(params: any): Observable<any> {
    const SupplierssUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/delete`;
    return this.http.delete<any>(SupplierssUrl, {params});
  }

  validateSuppliers(dataToValidate: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/update/state`;
    return this.http.put<any>(updateBillUrl, dataToValidate)
  }

  addBulkSuppliers(data: any): Observable<any> {
    const SupplierssUrl = `${environment.baseUrlProcurement}/api/v1/suppliers/create/bulk`;
    return this.http.post<any>(SupplierssUrl, data);
  }

    // ********************************************************************************************************************



}
